</template>

<script>
	export default {
		data() {
			return {

				userinfo: {},
				ouTstatus: false,
				mylistShow: false,
				black_view: false,
				overlayshow: false,
				Bdialogshow: false,
				keyword: '遥看瀑布挂前川',
				tabslist: [{
					name: '我收藏的',
					chcek: true,
					id: '01'
				}, {
					name: '我创建的',
					chcek: false,
					id: '02'
				}, {
					name: '我喜欢的',
					chcek: false,
					id: '03'
				}, ],
				CollectionUrlL: '/index/user/get_user_nft', //收藏列表Url
			}
		},
		onLoad() {
			this.$my.get('/index/user/get_user_msg').then(res => {
				console.log(res, '用户信息');
				this.userinfo = res.data;
			})

			this.tabslist.forEach(item => {
				if (item.chcek == true && item.id == '01') {
					this.getData(this.CollectionUrlL, {}, 'get', '01', '收藏列表');
				}
			})

		},
		methods: {

			getData(url, data, method, type, text) {

				if (method == 'post') {
					this.$my.post(url, data).then(res => {
						console.log(res, text);
					})
				} else {
					this.$my.get(url, data).then(res => {
						console.log(res, text);
					})
				}
			},

			api(url, data, method, text) {
				if (method == 'post') {
					this.$my.post(url, data).then(res => {
						console.log(res, text);
						return res;
					})
				} else {
					this.$my.get(url, data).then(res => {
						console.log(res, text);
						return res;
					})
				}
			},

			setup() {
				uni.navigateTo({
					url: '/pages/my/setup/setup'
				})
			},
			transfer() {
				console.log(123);
				uni.navigateTo({
					url: '/pages/my/Transfercollection/Transfercollection'
				})
				this.black_view = false;
			},
			goto_collection(url) {

				if (!url) {
					uni.navigateTo({
						url: '/pages/my/collection/collection'
					});
				} else {
					uni.navigateTo({
						url: url
					});
				}

				this.mylistShow = false;
			},
			LinkWallet() {
				this.ouTstatus = false; //未登录状态
				this.mylistShow = false;
			},
			loginOut() {
				// this.ouTstatus = true; //未登录状态 --- 正式使用
				uni.navigateTo({
					url: '/pages/login/login'
				})

			},
			mylist() {
				this.mylistShow = true;
			},
			overlayFn() {
				this.black_view = true;
			},
			Submit(val) {
				console.log(val, '提交');
			},
			searchFn() {
				this.Bdialogshow = true;
			},
			closeFn(val) {
				this.Bdialogshow = val;
			},
			open() {
				// console.log('open');
			},
			close() {
				this.mylistShow = false
				// console.log('close');
			},
			copy(value) {
				console.log(value);
				uni.setClipboardData({
					data: value, // e是你要保存的内容
					success: function() {
						uni.showToast({
							title: '复制成功',
							icon: 'none'
						})
					}
				})
			},
			tabsC(id) {
				this.tabslist.forEach(item => {
					if (item.id == id) {
						item.chcek = true;
					} else {
						item.chcek = false;
					}
				})
			},
			scrolltolower() {

			}
		},


	}
</script>

